.shadowContainer{
  box-shadow:  0px 2px 92px 0px rgba(0, 0, 0, 0.13);
  -webkit-box-shadow:0px 2px 92px 0px rgba(0, 0, 0, 0.13);
  -moz-box-shadow:0px 2px 92px 0px rgba(0, 0, 0, 0.13);
  
}

.main-content{
  background: white;
  min-height: 100vh;
  border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  margin-bottom: 30px;
  padding: 30px;
}

.title{
  position: relative;
  display: inline-block;
  padding: 0 3px;
}

.title  .titleText{
  font-size: 18px;
  font-weight: 600;
  position: relative;
  margin: 0;
}

.title span{
  position: absolute;
  height: 7px;
  width: 100%;
  bottom: 0;
  opacity: 0.4;
  left: 0;
  background: red;
}