/*Skills*/
.skill{
    border-radius: 8px;
    padding: 28px;
    box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    min-height:186px;
    height:100%;
    background-color: red;
}
.skill .skillTitle{
    font-size:18px;
    font-weight: 500;
    line-height: 35px;
}
.skill .skillDescription{
    display: flex;
    align-items: center;
    color: #989898;
    font-weight: 400;
    line-height: 18px;
}

.skill .skillDescription .timelineDot{
    color: black;
    border-color: red;
    padding: 2px;
    margin-right: 5px;
}