.ProFile{
    background: #fff;
    border-radius: 6px;
    width:100%;
    display: inline-block;
}
.ProfileName{
    line-height: 18px;
    padding: 20px;
}

.ProfileName .Name{
    text-transform: uppercase;
    font-size: 17px;
    font-weight: bold;
}
.ProfileName .NickName{
    font-size: 13px;
    color:#777;
}

.ProfileImage{
    margin-top: 10px;
}

.ProfileImage img{
    width: 100%;
}
.ProfileDetail{
    font-size: 13.5px;
    color:#787878;
    font-weight: 400;
    
}

.ProfileDetail span{
    color: black;
}
.ProFile .ButtonContainer{
    width:100%;
    display: flex ;
    justify-content: center ;
    margin-top: 10px;
}